Gumify.generate = function () {
  var code = JSON.parse(Blockly.JavaScript.workspaceToCode(workspace));
  var xml = Blockly.Xml.workspaceToDom(workspace);
  var stack = Gumify.stack;

  for (var key in stack) {
    if (workspace.getBlockById(key) === null) {
      delete stack[key];
    }
  }

  return {
    code: code,
    xml: Blockly.Xml.domToText(xml),
    stack: stack
  };
};

Gumify.compile = function () {
  var code = Blockly.JavaScript.workspaceToCode(workspace);
  return JSON.parse(code);
};

Gumify.updateLastBlock = function (value) {
  if (value === null || value === "null" || value === "") {
    var block = workspace.getBlockById(Gumify.lastBlockId);
    block.dispose();
    return null;
  }

  var selector = atob(value);
  Gumify.stack[Gumify.lastBlockId] = selector;
  return selector;
};

Gumify.updateWorkspace = function (xml, stack) {
  var xmlFinal = decodeURIComponent(escape(window.atob(xml)));
  var stackFinal = decodeURIComponent(escape(window.atob(stack)));
  Gumify.stack = JSON.parse(stackFinal);
  Blockly.Xml.domToWorkspace(Blockly.Xml.textToDom(xmlFinal), workspace);
};

